* {
  box-sizing: border-box;
}

html {
  font-size: 10px;
}

body {
  margin: 0;
  padding: 0;
  background-color: rgb(244,244,244);
  color: #333;
}

p {
  font-size: 1.6rem;
}

small {
  font-size: 1.2rem;
}

.container {
  display: block;
  margin: 0 auto;
  max-width: 1000px;
  padding: 40px;
}

header {
  font-size: 4.5rem;
  text-align: center;
  margin-bottom: 20px;
  color: #6B5CA5;
}

.card--title {
  color: #6B5CA5;
  text-align: center;
}

.form {
  display: grid;
}

.input {
  border-radius: 20px;
  border: 1px solid #ddd;
  padding: 1rem 2rem;
  font-size: 1.8rem;
  margin-bottom: 10px;
  color: #333;
}

.button {
  border-radius: 20px;
  border: none;
  background-color: #FFAD05;
  color: white;
  padding: 1rem 2rem;
  font-size: 1.8rem;
  cursor: pointer;
  transition: background-color 250ms;
}

.button:hover {
  background-color: rgb(231, 156, 4);
}

.card-list {
  margin-top: 4rem;
}

.card {
  padding: 2rem 4rem;
  border-radius: 10px;
  box-shadow: 1px 1px 5px rgba(0,0,0,0.25);
  margin-bottom: 2rem;
  background-color: white;
}

.card--title {
  margin-bottom: 0.5rem;
  font-size: 3.2rem;
}

.card--image {
  display: block;
  margin: 0px auto;
}

.movie--desc {
  line-height: 1.5;
}



@media (min-width:786px) {
  .form {
    grid-template-columns: 1fr auto;
    grid-gap: 1rem;
    align-items: center;
  }

  .card {
    display: grid;
    grid-template-columns: auto 1fr;
  } 

  .card--image {
    margin-right: 15px;
  }

  .input {
    margin-bottom: 0px;
  }

  .card--title {
    text-align: start;
  }
}